
@use 'base/variables' as v;
@use 'base/mixins' as m;

.video {
    position: relative;
    min-height:50rem;
    max-height: 70 rem;
    overflow: hidden;
    .overlay{
        position:absolute;
        background: linear-gradient(270deg, rgba(v.$morado, 10%) );
        inset:0;

        display: flex;
        align-items: center;

    }

    video{
        display:block;
        width:auto;
        @include m.desktop{
            width: 100%;
        }
    }
}

.contenido-video{
    z-index:10;


    h2{
        color:v.$blanco;
        font-size: 7rem;
        margin:0;

    }


    p{
        background-color: v.$rosa;
        display:inline-block;
        padding:1rem 2rem;
      
        color:v.$blanco;
        font-size: 2.4rem;
        font-weight: 700;
        margin:0;



    }
}